export default {
  name: "list",
  props: {
    list: {
      type: Array,
      default: () => []
    },
    showPic: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    selectItem(index, item) {
      this.$emit("select", index, item);
    }
  }
};



